$footerBgColor: #393e6b;
$footerSmallTextColor1: #aeb1c3;
$footerSmallTextColor2: #fff;


.Footer {
    background-color: $footerBgColor;
    padding-top: 62px;
    padding-bottom: 62px;
    position: relative;
  }
  
  .Footer-smallText {
    color: $footerSmallTextColor1;
    font-size: 15px;
  }
  
  .u-whiteText {
    color: $footerSmallTextColor2;
  }
  
  .Footer-bigText {
    font-size: 16px;
    color: $footerSmallTextColor2;
  }
  
  .Footer-Logo {
    max-height: 35px;
    max-width: 150px;
  }
  
  .Footer-Icon {
    max-width: 26px;
    color: $footerSmallTextColor1;
    margin-right: 25px;
  }
  
  .Footer-Navigation {
    list-style-type: none;
    text-align: start;
  }
  
  .Navigation-Menu--footerRightLayout {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    text-align: start;
  }
  
  .u-spaceBetween {
    display: flex;
    justify-content: space-between;
  }
  
  .Footer-liMainNumber{
    font-size: 25px;
    color: $footerSmallTextColor2;
    padding-bottom: 24px;
  }
  
  .Footer-liNumber{
    font-size: 22px;
    padding-bottom: 13px;
  }
  
  @media (max-width: 769px) { 
    .Footer-mobileHide {
      display: none;
    }
    .Footer-Navigation{
      padding: 59px 0 52px 44px;
      
    }
    .Footer-smallText {
      font-size: 28px;
    }
  }
  
  .Footer-smallText--emailS{
    margin-bottom: 19px;
    margin-top: 11px;
  }
  
  .Footer-contactsCity{
    padding-bottom: 29px;
  }
  
  .Footer-schedule{
    padding-bottom: 22px;
  }
  
  .Footer-ChatButton{
    max-width: 57px;
  }
  
  @media (max-width: 769px) { 
    .Footer-ChatButton {
      display: none;
    }
  }

  @media (max-width: 769px) { 
    .Footer-smallText {
      font-size: 1rem;
    }
    .Footer{
      padding-top: 0;
      padding-bottom: 0;
    }
  }

